<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar flat color="white">
                <v-toolbar-title>Ingresos</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-text-field v-if="verNuevo==0" class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
                <v-btn v-if="verNuevo==0" @click="listar()" color="primary" dark class="mb-2">Buscar</v-btn>
                <v-spacer></v-spacer>
                <!-- <template v-slot:activator="{ on }">
                    <v-btn color="primary" dark class="mb-2" v-on="on">Nuevo</v-btn>
                </template> -->

                <v-btn v-if="verNuevo==0" @click="mostrarNuevo" color="primary" dark class="mb-2">Nuevo</v-btn>
                <!-- Modal para seleccionar los articulos y agregarlos al data-table articulos -->
                <v-dialog v-model="verArticulos" max-width="1000px">
                    <v-card>
                        <v-card-title>
                            <span>Seleccione un artículo</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container grid-list-md>
                                <v-layout wrap>
                                    <v-flex xs12 sm12 md12 lg12 xl12>
                                        <!-- caja de busqueda de los articulos -->
                                        <v-text-field append-icon="search" class="text-xs-center" v-model="texto"
                                        label="Ingrese artículo a buscar" @keyup.enter="listarArticulo()">
                                        </v-text-field>
                                        <template>
                                            <!-- data-table de los articulos -->
                                            <v-data-table
                                                :headers="cabeceraArticulos"
                                                :items="articulos"
                                                class="elevation-1"
                                            >
                                                <template v-slot:items="props">
                                                    <td class="justify-center layout px-0">
                                                        <v-icon
                                                        small
                                                        class="mr-2"
                                                        @click="agregarDetalle(props.item)"
                                                        >
                                                        add
                                                        </v-icon>     
                                                    </td>
                                                    <td>{{ props.item.nombre }}</td>
                                                    <td>{{ props.item.categoria}}</td>
                                                    <td>{{ props.item.descripcion}}</td>
                                                    <td>{{ props.item.stock}}</td>
                                                    <td>{{ props.item.precio}}</td>
                                                    
                                                </template>
                                                <template v-slot:no-data>
                                                    <h3>No hay artículos para mostrar.</h3>
                                                </template>
                                            </v-data-table>
                                            <!-- fin data-table de los articulos -->
                                        </template>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn @click="ocultarArticulos()" color="blue darken" flat>
                                Cancelar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>

                <!-- Modal para activar o desactivar -->
                <v-dialog v-model="adModal" max-width="500px">
                    <v-card>
                        <v-card-title class="headline" v-if="adAccion==1">¿Activar Item?</v-card-title>
                        <v-card-title class="headline" v-if="adAccion==2">¿Anular Ingreso?</v-card-title>
                        <v-card-text>
                            Estás a punto de 
                            <span v-if="adAccion==1">Activar</span>
                            <span v-if="adAccion==2">Anular</span>
                            el ítem {{adNombre}}
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" flat="flat" @click="activarDesactivarCerrar">
                                Cancelar
                            </v-btn>
                            <v-btn v-if="adAccion==1" color="orange darken-4" flat="flat" @click="activar">
                                Activar
                            </v-btn>
                            <v-btn v-if="adAccion==2" color="orange darken-4" flat="flat" @click="desactivar">
                                Anular
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>

            </v-toolbar>
            <!-- busca la variable asociada al método :search="search" -->
            <v-data-table
                :headers="headers"
                :items="ingresos"
                :search="search"
                class="elevation-1"
                v-if="verNuevo==0"
            >
                <template v-slot:items="props">
                    <td class="justify-center layout px-0">
                        <v-icon
                        small
                        class="mr-2"
                        @click="verDetalles(props.item)"
                        >
                        tab
                        </v-icon>
                        <template v-if="props.item.estado == 'Aceptado'">
                            <v-icon
                            small
                            @click="activarDesactivarMostrar(2,props.item)"
                            >
                            block
                            </v-icon>
                        </template>      
                    </td>
                    <td>{{ props.item.usuario }}</td>
                    <td>{{ props.item.proveedor }}</td>
                    <td>{{ props.item.tipo_comprobante }}</td>
                    <td>{{ props.item.serie_comprobante }}</td>
                    <td>{{ props.item.num_comprobante }}</td>
                    <td>{{ props.item.fecha_hora }}</td>
                    <td>{{ props.item.impuesto }}</td>
                    <td>{{ props.item.total }}</td>
                    <td>
                        <div v-if="props.item.estado=='Aceptado'">
                            <span class="blue--text">Aceptado</span>
                        </div>
                        <div v-else>
                            <span class="red--text">{{props.item.estado}}</span>
                        </div>
                    </td>    
                </template>
                <template v-slot:no-data>
                    <v-btn color="primary" @click="listar">Resetear</v-btn>
                </template>
            </v-data-table>

            <!-- Datos del detalle de ingreso -->
            <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
                <v-layout row wrap>

                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-select v-model="tipo_comprobante"
                        :items="comprobantes" label="Tipo comprobante">
                        </v-select>
                    </v-flex>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-text-field v-model="serie_comprobante" label="Serie comprobante">
                        </v-text-field>
                    </v-flex>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-text-field v-model="num_comprobante" label="Número comprobante">
                        </v-text-field>
                    </v-flex>
                    
                    <v-flex xs12 sm8 md8 lg8 xl8>
                        <v-select v-model="idproveedor"
                        :items="proveedores" label="Proveedor">
                        </v-select>
                    </v-flex>
                    <v-flex xs12 sm4 md4 lg4 xl4>
                        <v-text-field type="number" v-model="impuesto" label="impuesto">
                        </v-text-field>
                    </v-flex>

                    <v-flex xs12 sm8 md8 lg8 xl8>
                        <v-text-field @keyup.enter="buscarCodigo()" v-model="codigo" label="Código">
                        </v-text-field>
                    </v-flex>
                    <v-flex xs12 sm2 md2 lg2 xl2>
                        <v-btn @click="mostrarArticulos()" small fab dark color="teal">
                            <v-icon dark>list</v-icon>
                        </v-btn>
                    </v-flex>
                    <!--se muestra unmensaje de error al no encontrarse un articulo-->
                    <v-flex xs12 sm2 md2 lg2 xl2 v-if="errorArticulo">
                        <div class="red--text" v-text="errorArticulo">
                        </div>
                    </v-flex>
                    
                    <!-- v-flex de la tabla detalles -->
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <!-- data-table detalles-->
                        <v-data-table
                            :headers="cabeceraDetalles"
                            :items="detalles"
                            hide-actions
                            class="elevation-1"
                        >
                            <template v-slot:items="props">
                                <td class="justify-center layout px-0">
                                    <v-icon
                                    small
                                    class="mr-2"
                                    @click="eliminarDetalle(detalles,props.item)"
                                    >
                                    delete
                                    </v-icon>     
                                </td>
                                <td>{{ props.item.articulo }}</td>
                                <td>
                                    <v-text-field type="number" v-model="props.item.cantidad">
                                    </v-text-field>
                                </td>
                                <td>
                                    <v-text-field type="number" v-model="props.item.precio">
                                    </v-text-field>
                                </td>
                                <td>${{ props.item.cantidad * props.item.precio }}</td>  
                            </template>
                            <template v-slot:no-data>
                                <h3>No hay artículos agregados al detalle.</h3>
                            </template>
                        </v-data-table>
                        <!-- fin-data-table detalles del ingreso-->
                        <v-flex class="text-xs-right">
                            <strong>Total Parcial:</strong> ${{totalParcial=(total-totalImpuesto).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                            <strong>Total Impuesto:</strong> ${{totalImpuesto=((total*impuesto)/(100+impuesto)).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                            <strong>Total Neto:</strong> ${{total=(calcularTotal).toFixed(2)}}
                        </v-flex>
                    </v-flex>

                    <!-- mostrar mensajes de validcion  -->
                    <v-flex xs12 sm12 md12 lg12 xl12 v-show="valida">
                        <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v">
                        </div>
                    </v-flex>

                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-btn @click="ocultarNuevo()" color="blue darken-1" flat>Cancelar</v-btn>
                        <v-btn v-if="verDet==0" @click="guardar()" color="success" flat>Guardar</v-btn>
                    </v-flex>
                    
                </v-layout>
            </v-container>

        </v-flex>
    </v-layout>
</template>

<script>
//importar la dependencia axios
import axios from 'axios'
export default {
    data() {
        return{
            //variables de uso del sistema
            ingresos : [],
            search : '',
            id : '',
            idproveedor: '',
            proveedores: [],
            tipo_comprobante: '',
            comprobantes: ['FACTURA','BOLETA','TICKET','GUIA'],
            serie_comprobante: '',
            num_comprobante: '',
            impuesto: 16,
            codigo: '',
            //variables modal de los detalles del ingreso
            cabeceraDetalles: [ //cabecera del data-table los detalles
                { text: 'Borrar', value: 'borrar', sortable: false },
                { text: 'Artículo', value: 'articulo', sortable: false },
                { text: 'Cantidad', value: 'cantidad', sortable: false },
                { text: 'Precio', value: 'precio', sortable: false },
                { text: 'Subtotal', value: 'subtotal', sortable: false }
            ],
            detalles:[],
            verDet: 0,
            verNuevo:0,
            errorArticulo: null,
            totalParcial: 0,
            totalImpuesto: 0,
            total: 0, 
            //variables para el modal articulos
            cabeceraArticulos: [//Cabecera para el modal de articulos
                { text: 'Seleccionar', value: 'seleccionar', sortable: false },
                { text: 'Artículo', value: 'articulo' },
                { text: 'categoría', value: 'categoria' },
                { text: 'Descripción', value: 'descripcion', sortable: false },
                { text: 'Stock', value: 'stock', sortable: false },
                { text: 'Precio venta', value: 'precio_venta', sortable: false }
            ],
            articulos: [],
            texto: '',
            verArticulos: 0,
            //Variables para la validacion de campos
            valida: 0,
            validaMensaje: [],
            //variables para el modal activar o desactivar
            adModal: 0,
            adAccion: 0,
            adNombre: '',
            adId: '',

            //variables del data-table
            dialog: false,
            headers: [
                { text: 'Opciones', value: 'opciones', sortable: false },
                { text: 'Usuario', value: 'usuario' },
                { text: 'Proveedor', value: 'proveedor' },
                { text: 'Tipo comprobante', value: 'tipo_comprobante' },
                { text: 'Serie comprobante', value: 'serie_comprobante', sortable: false },
                { text: 'Número comprobante', value: 'num_comprobante', sortable: false },
                { text: 'Fecha', value: 'fecha_hora', sortable: false },
                { text: 'Impuesto', value: 'impuesto', sortable: false },
                { text: 'Total', value: 'total', sortable: false },
                { text: 'Estado', value: 'estado', sortable: false }
            ],
            // end return    
        }
    },
    computed: {
        calcularTotal: function(){
            var resultado = 0.0;
            for(var i = 0; i < this.detalles.length; i++){
                resultado = resultado+(this.detalles[i].precio * this.detalles[i].cantidad);
            }
            return resultado;
        }
    },
    watch: {
        dialog (val) {
        val || this.close()
        }
    },
    created () {
        this.listar();
        this.select();
    },

    methods: {
        //métodos del sistema
        mostrarNuevo(){
            this.verNuevo = 1;
        },
        ocultarNuevo(){
            this.verNuevo = 0;
            this.limpiar();
        },
        buscarCodigo(){
            let me = this;
            me.errorArticulo = null;
            //variables para el envío del token de autorización
            let header = {"Authorization" : "Bearer " + this.$store.state.token};
            let configuracion = {headers : header};
            axios.get('api/Articulos/BuscarCodigoIngreso/'+this.codigo, configuracion)
            .then(function(response){
                me.agregarDetalle(response.data);
            })
            .catch(function(error){
                console.log(error)
                me.errorArticulo = 'No existe el artículo.';
            });
        },
        listarArticulo(){
            let me = this;
            //variables para el envío del token de autorización
            let header = {"Authorization" : "Bearer " + this.$store.state.token};
            let configuracion = {headers : header};
            axios.get('api/Articulos/ListarIngreso/'+me.texto, configuracion)
            .then(function(response){
                me.articulos = response.data;
            })
            .catch(function(error){
                console.log(error)
            });
        },
        mostrarArticulos(){
            this.verArticulos = 1;
        },
        ocultarArticulos(){
            this.verArticulos = 0;
        },
        agregarDetalle(data = []){
            this.errorArticulo = null;
            if(this.encuentra(data['idarticulo'])){
                this.errorArticulo = 'El artículo ya ha sido agregado.';
            }
            else{
                this.detalles.push(
                    {
                        idarticulo: data['idarticulo'],
                        articulo: data['nombre'],
                        cantidad: 1,
                        precio: 1
                    }
                );
            }

            
        },
        encuentra(id){
            var sw = 0;
            for(var i =0; i < this.detalles.length; i++){
                if(this.detalles[i].idarticulo == id){
                    sw = 1;
                }
            }
            return sw;
        },
        listar(){
            let me = this;
            //variables para el envío del token de autorización
            let header = {"Authorization" : "Bearer " + this.$store.state.token};
            let configuracion = {headers : header};
            let url = '';
            if(!me.search){//si no escribe texto
                url = 'api/Ingresos/Listar';
            }
            else{//ingresa si escribe texto
                url = 'api/Ingresos/ListarFiltro/'+me.search;
            }

            axios.get(url, configuracion)
            .then(function(response){
                me.ingresos = response.data;
            })
            .catch(function(error){
                console.log(error)
            });
        },
        listarDetalles(id){
            let me = this;
            //variables para el envío del token de autorización
            let header = {"Authorization" : "Bearer " + this.$store.state.token};
            let configuracion = {headers : header};
            axios.get('api/Ingresos/ListarDetalles/'+id, configuracion)
            .then(function(response){
                me.detalles = response.data;
            })
            .catch(function(error){
                console.log(error)
            });
        },
        verDetalles(item){
            this.limpiar();
            this.tipo_comprobante = item.tipo_comprobante;
            this.serie_comprobante = item.serie_comprobante;
            this.num_comprobante = item.num_comprobante;
            this.idproveedor = item.idproveedor;
            this.impuesto = item.impuesto;
            this.listarDetalles(item.idingreso);
            this.verNuevo = 1; 
            this.verDet = 1;
        },
        eliminarDetalle(arr, item){
            var i = arr.indexOf(item);
            if(i !== -1){
                arr.splice(i,1);
            }
        },
        select(){
            let me = this;
            var proveedoresArray = [];
            //variables para el envío del token de autorización
            let header = {"Authorization" : "Bearer " + this.$store.state.token};
            let configuracion = {headers : header};
            axios.get('api/Personas/SelectProveedores',configuracion)
            .then(function(response){
                proveedoresArray = response.data;
                proveedoresArray.map(function(x){
                    me.proveedores.push({text: x.nombre,value: x.idpersona})
                });
            })
            .catch(function(error){
                console.log(error)
            });
        },
        limpiar(){
            this.id = "";
            this.idproveedor = "";
            this.tipo_comprobante = "";
            this.serie_comprobante = "";
            this.num_comprobante = "";
            this.impuesto = 16;
            this.codigo = "";
            this.detalles = [];
            this.total = 0;
            this.totalImpuesto = 0;
            this.totalParcial = 0;
            this.verDet = 0;
        },
        guardar() {
            if(this.validar()){
                return;
            }

            //variables para el envío del token de autorización
            let header = {"Authorization" : "Bearer " + this.$store.state.token};
            let configuracion = {headers : header};

            //código para guardar
            let me = this;
            axios.post('api/Ingresos/Crear',{
                'idproveedor':me.idproveedor,
                'idusuario':me.$store.state.usuario.idusuario,
                'tipo_comprobante':me.tipo_comprobante,
                'serie_comprobante':me.serie_comprobante,
                'num_comprobante':me.num_comprobante,
                'impuesto':me.impuesto,
                'total':me.total,
                'detalles':me.detalles
            },configuracion)
            .then(function(response){
                me.ocultarNuevo();
                me.listar();
                me.limpiar();
            })
            .catch(function(error){
                console.log(error)
            });
        },
        validar(){
            this.valida=0;
            this.validaMensaje=[];

            if(!this.idproveedor){
                this.validaMensaje.push("Seleccione un proveedor.");
            }
            if(!this.tipo_comprobante){
                this.validaMensaje.push("Seleccione un tipo de comprobante.")
            }
            if(!this.num_comprobante){
                this.validaMensaje.push("Ingrese el número del comprobante.")
            }
            if(!this.impuesto || this.impuesto < 0){
                this.validaMensaje.push("Ingrese un impuesto válido.")
            }
            if(this.detalles.length <= 0){
                this.validaMensaje.push("Ingrese al menos un artículo al detalle.")
            }

            if(this.validaMensaje.length){
                this.valida = 1;
            }

            return this.valida;
        },
        activarDesactivarMostrar(accion, item){
            this.adModal = 1;
            this.adNombre = item.num_comprobante;
            this.adId = item.idingreso;

            if(accion == 1){
                this.adAccion = 1;//activar
            }
            else if(accion == 2){
                this.adAccion = 2;//desactivar
            }
            else{
                this.adModal = 0;//no mostrar el modal
            }
        },
        activarDesactivarCerrar(){
            this.adModal = 0;
        },
        /*activar(){
            let me = this;
            //variables para el envío del token de autorización
            let header = {"Authorization" : "Bearer " + this.$store.state.token};
            let configuracion = {headers : header};
            axios.put('api/Usuarios/Activar/'+this.adId,{},configuracion)
            .then(function(response){
                me.adModal=0;
                me.adAccion=0;
                me.adNombre="";
                me.adId="";
                me.listar();
            })
            .catch(function(error){
                console.log(error)
            });
        },*/
        desactivar(){
            let me = this;
            //variables para el envío del token de autorización
            let header = {"Authorization" : "Bearer " + this.$store.state.token};
            let configuracion = {headers : header};
            axios.put('api/Ingresos/Anular/'+this.adId,{},configuracion)
            .then(function(response){
                me.adModal=0;
                me.adAccion=0;
                me.adNombre="";
                me.adId="";
                me.listar();
            })
            .catch(function(error){
                console.log(error)
            });
        },
        //End methods 
    }
}
</script>






